import React, { useState, useEffect } from "react";
import { Row, Col, Table, Form } from "react-bootstrap";
import { TextField } from "@material-ui/core";
import { ethers } from "ethers";
import { toast } from "react-toastify";
import SocialLinkinCard from "../SocialLinkinCard";
import PropTypes from "prop-types";

import Countdown from "react-countdown";
import { usePrivateSaleApi } from "../../hooks/usePrivateSaleApi";
import { useAccount } from "../../hooks/useAccount";
import { useAppLoading } from "../../hooks/useAppLoading";

export default function PrivateCard({ pool }) {
  const {
    joinPool,
    getBalanceofContract,
    getContractMampBalance,
    getStartTime,
    getEndTime,
    getContribution,
    getDepositedBNB,
  } = usePrivateSaleApi();
  const { account, getBalanceBNB } = useAccount();
  const { dispatchLoading, dispatchNotLoading } = useAppLoading();
  const [amount, setAmount] = useState(0);
  const [contribution, setContribution] = useState(0);
  const [balanceBNB, setBalanceBNB] = useState("");
  const [isOpen, setIsOpen] = useState(false);
  const [startTime, setStartTime] = useState("");
  const [endTime, setEndTime] = useState("");
  const [contributionMamp, setContributionMamp] = useState("");
  const [balanceofContract, setBalanceofContract] = useState("");
  const [contractMampBalance, setContractMampBalance] = useState("");
  const [depositedBNB, setDepositedBNB] = useState("");
  const [sellingNow, setSellingNow] = useState(false);
  const [finishNow, setFinishNow] = useState(false);
  const decimal = 10 ^ 8;

  const startTimeEpoch = startTime.toString() * 1.0 * 1000;
  const endTimeEpoch = endTime.toString() * 1.0 * 1000;
  const duration = endTimeEpoch - startTimeEpoch;
  const startUTCtime = new Date(startTimeEpoch).toUTCString();
  const endUTCtime = new Date(endTimeEpoch).toUTCString();
  const timeOut = startTimeEpoch - Date.now();
  const getLocalTime = (s) => localStorage.getItem(s) * 1.0;

  useEffect(() => {
    console.log("firsteffect");
    getBalanceBNB().then(setBalanceBNB);
    const fetchInfo = async () => {
      dispatchLoading();
      if (account) {
        const gc = await getContribution(account);
        setContributionMamp(gc.toString());
        console.log("contrimamp", contributionMamp);
      }
      // const gbc = await getBalanceofContract();
      // setBalanceofContract(ethers.utils.formatUnits(gbc*1.0,"8"));
      await getBalanceofContract().then(setBalanceofContract);
      await getContractMampBalance().then(setContractMampBalance);
      const gdb = await getDepositedBNB();
      setDepositedBNB(ethers.utils.formatEther(gdb.toString()));
      await getStartTime().then(setStartTime);
      await getEndTime().then(setEndTime);
      dispatchNotLoading();
    };
    fetchInfo();
  }, [
    account,
    // getBalanceofContract,
    // getContractMampBalance,
    // getStartTime,
    // getEndTime,
    // getContribution,
  ]);

  const handleAmount = (e) => {
    console.log(e.target.value);
    if (e.target.value === '') {
      setAmount("");
      setContribution("");
    } else {
      const value = e.target.value * 1.0;
      setAmount(value);
      setContribution(value * 100000 * 0.94);
    }
  };
  const handleContribution = (e) => {
    const value = e.target.value * 1.0;
    setContribution(value);
    setAmount(value / 100000);
  };

  const jointoPool = async () => {
    const amountofBNB = ethers.utils.parseEther(amount.toString());
    if(!sellingNow) {
      toast("You Should Wait Until Open Pool...");
      return;
    }
    if (amount > 2 || amount < 0.1) {
      toast("Min Amount: 0.1 BNB. Max Amount: 2 BNB");
      return;
    }
    if (balanceBNB < amountofBNB) {
      toast("Sorry. Your Fund is not Enough... Check Wallet.");
      return;
    }
    try {
      const amountToBuy = await joinPool({
        value: amountofBNB,
      });
      console.log("success", amountToBuy.value.toString());
    } catch (err) {
      console.log(err);
      // toast(err.data.message)
    } finally {
    }
  };

  setTimeout(() => {
    setIsOpen(true);
  }, timeOut);

  useEffect(() => {
    console.log('useeffect below')
    localStorage.setItem("start_epoch_time", startTimeEpoch);
    localStorage.setItem("end_epoch_time", endTimeEpoch);
    if (timeOut > 0) {
      console.log(sellingNow);
      setSellingNow(false);
    } else {
      console.log(sellingNow);
      localStorage.removeItem("start_epoch_time");
      setSellingNow(true);
    }
    if (
      startTimeEpoch < Date.now() &&
      endTimeEpoch > Date.now() &&
      timeOut < 0
    ) {
      console.log(sellingNow);
      setSellingNow(true);
    }
    if (endTimeEpoch < Date.now()) {
      console.log(sellingNow);
      localStorage.removeItem("end_epoch_time");
      setSellingNow(false);
      setFinishNow(true);
    }
  }, [startTime, endTime]);

  return (
    <>
      {/* <section className="pool-card">
        <div className="pool-card-header">
          <a href="/">
            <img src={pool.picture} alt="pool profile"></img>
          </a>
          <div className="pool-card-title">
            <strong>{pool.name}</strong>
            <SocialLinkinCard />
          </div>
          <p style={{ color: "orange" }}></p>
        </div>
        <div className="pool-card-body">
          <section>
            <h4 style={{ marginBottom: 10, textAlign: "center" }}>
              Pool Mamp Amount:
              {ethers.utils.formatUnits(
                contractMampBalance * 1.0,
                "8"
              )} MAMP{" "}
            </h4>
            <h4 style={{ marginBottom: 10, textAlign: "center" }}>
              Pool BNB Amount: {depositedBNB} BNB{" "}
            </h4>
            <h4 style={{ marginBottom: 10, textAlign: "center" }}>
              Start at: {startUTCtime}
            </h4>
            <h4 style={{ marginBottom: 0, textAlign: "center" }}>
              {!sellingNow && (
              <Countdown
                date={getLocalTime("start_epoch_time")}
                // daysInHours
              />
              )}
            </h4>
            <Row style={{ marginTop: "20px" }}>
              <Col lg={7} style={{ marginTop: "10px" }}>
                <Table
                  className="allocation-table"
                  bordered
                  hover
                  variant="dark"
                >
                  <thead>
                    <tr>
                      <th colspan="2">ALLOCATION(BNB)</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>MIN</td>
                      <td>{pool.min_allocation}</td>
                    </tr>
                    <tr>
                      <td>MAX</td>
                      <td>{pool.max_allocation}</td>
                    </tr>
                  </tbody>
                  <thead>
                    <tr>
                      <th colspan="2">PRICE(USDT)</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>IDO</td>
                      <td>{pool.ido_price}</td>
                    </tr>
                    <tr>
                      <td>LISTING</td>
                      <td>{pool.listing_price}</td>
                    </tr>
                  </tbody>
                </Table>
              </Col>
              <Col lg={5} style={{ padding: "0 30px" }}>
                <h3 style={{ marginBottom: 0, fontSize: "14px" }}>
                  AMOUNT(BNB)
                </h3>
                <Form.Control
                  style={{ marginBottom: "20px" }}
                  required
                  value={amount}
                  type="number"
                  onChange={handleAmount}
                />
                <div className="join-button">
                  <button class="poolcard-button" onClick={jointoPool}>
                    JOIN POOL
                  </button>
                </div>
                <h3
                  style={{
                    marginBottom: 0,
                    marginTop: "70px",
                    fontSize: "14px",
                  }}
                >
                  AMOUNT(MAMP)
                </h3>
                <Form.Control
                  type="number"
                  value={contribution}
                  onChange={handleContribution}
                />
              </Col>
            </Row>
            <Row style={{ marginTop: "30px" }}>
              <h3 style={{ marginBottom: 0, fontSize: "14px" }}>COUNTDOWN</h3>
              <div style={{ fontSize: "2em" }}>
                {sellingNow &&!finishNow&& (
                <Countdown
                  date={getLocalTime("end_epoch_time")}
                  autoStart={() => {}}
                  onComplete={() => {}}
                  // daysInHours
                />
                )}
              </div>
            </Row>
          </section>
        </div>
      </section> */}
    </>
  );
}

PrivateCard.propTypes = {
  pool: PropTypes.object.isRequired,
};
